/* ============================================
   LOADING SKELETON WITH SHIMMER
   ============================================ */

   .loading-skeleton {
    padding: 30px;
    animation: fadeIn 0.3s ease;
  }
  
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  /* Shimmer Animation */
  .shimmer {
    position: relative;
    overflow: hidden;
    background: rgba(255, 255, 255, 0.05);
  }
  
  .shimmer::after {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.08),
      transparent
    );
    animation: shimmerMove 1.5s infinite;
  }
  
  @keyframes shimmerMove {
    0% { left: -100%; }
    100% { left: 100%; }
  }
  
  /* Skeleton Elements */
  .skeleton-line {
    border-radius: 6px;
    margin-bottom: 12px;
  }
  
  .skeleton-line.title {
    width: 60%;
    height: 36px;
    margin-bottom: 16px;
  }
  
  .skeleton-line.subtitle {
    width: 80%;
    height: 20px;
  }
  
  .skeleton-line.heading {
    width: 40%;
    height: 24px;
    margin-top: 30px;
    margin-bottom: 16px;
  }
  
  .skeleton-line.text {
    width: 100%;
    height: 16px;
  }
  
  .skeleton-line.text.short {
    width: 65%;
  }
  
  .skeleton-line.code {
    height: 14px;
    margin-bottom: 8px;
  }
  
  /* Badges */
  .skeleton-header {
    margin-bottom: 25px;
  }
  
  .skeleton-badges {
    display: flex;
    gap: 10px;
    margin-bottom: 30px;
    flex-wrap: wrap;
  }
  
  .skeleton-badge {
    width: 80px;
    height: 28px;
    border-radius: 20px;
  }
  
  /* Content */
  .skeleton-content {
    margin-bottom: 30px;
  }
  
  /* Cards */
  .skeleton-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    margin-bottom: 30px;
  }
  
  .skeleton-card {
    height: 100px;
    border-radius: 12px;
  }
  
  /* Code Block */
  .skeleton-code {
    border-radius: 12px;
    overflow: hidden;
    border: 1px solid rgba(255, 255, 255, 0.05);
  }
  
  .skeleton-code-header {
    height: 45px;
    background: rgba(255, 255, 255, 0.03);
  }
  
  .skeleton-code-body {
    padding: 20px;
    background: rgba(0, 0, 0, 0.2);
  }
  
  /* Responsive */
  @media (max-width: 768px) {
    .skeleton-cards {
      grid-template-columns: 1fr;
    }
  
    .skeleton-line.title {
      width: 80%;
    }
  }