/* ============================================
   ARCHITECTURE DIAGRAM - ANIMATED SVG
   ============================================ */

   .architecture-diagram {
    background: linear-gradient(135deg, rgba(30, 30, 30, 0.9) 0%, rgba(20, 20, 20, 0.95) 100%);
    border-radius: 16px;
    padding: 25px;
    margin: 25px 0;
    border: 1px solid rgba(102, 126, 234, 0.2);
    position: relative;
    overflow: hidden;
  }
  
  .architecture-diagram::before {
    content: '';
    position: absolute;
    inset: 0;
    background: radial-gradient(circle at 50% 50%, rgba(102, 126, 234, 0.05) 0%, transparent 70%);
    pointer-events: none;
  }
  
  .diagram-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .diagram-header h3 {
    margin: 0;
    color: #fff;
    font-size: 16px;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .diagram-header h3::before {
    content: 'ðŸ—ï¸';
  }
  
  .animate-btn {
    padding: 6px 14px;
    background: rgba(102, 126, 234, 0.2);
    border: 1px solid rgba(102, 126, 234, 0.4);
    border-radius: 20px;
    color: #a5b4fc;
    font-size: 12px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .animate-btn:hover {
    background: rgba(102, 126, 234, 0.3);
  }
  
  .animate-btn.active {
    background: rgba(74, 222, 128, 0.2);
    border-color: rgba(74, 222, 128, 0.4);
    color: #4ade80;
  }
  
  /* SVG Diagram */
  .diagram-svg {
    width: 100%;
    height: 300px;
    overflow: visible;
  }
  
  /* Nodes */
  .node {
    cursor: pointer;
    /* transition: transform 0.3s ease; */
  }
  
  /* .node:hover {
    transform: scale(1.15);
  } */
  
  .node-circle {
    fill: linear-gradient(135deg, #667eea, #764ba2);
    stroke: rgba(255, 255, 255, 0.3);
    stroke-width: 0.5;
    filter: url(#glow);
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275); /* Bouncy effect */
}
  
  .node.active .node-circle {
    fill: #4ade80;
    stroke: #fff;
    stroke-width: 1;
  }
  
  .node-icon {
    font-size: 4px;
    fill: white;
  }
  
  .node-label {
    font-size: 2.5px;
    fill: #a5b4fc;
    font-weight: 600;
  }
  
  .node.active .node-label {
    fill: #fff;
  }
  
  /* Connections */
  .connection-line {
    stroke: url(#connectionGradient);
    stroke-width: 0.4;
    fill: none;
    opacity: 0.6;
    transition: all 0.3s ease;
  }
  
  .connection.animating .connection-line {
    stroke-dasharray: 2 1;
    animation: dashMove 1s linear infinite;
  }
  
  @keyframes dashMove {
    to {
      stroke-dashoffset: -3;
    }
  }
  
  .connection-label {
    font-size: 1.8px;
    fill: rgba(255, 255, 255, 0.5);
    text-anchor: middle;
  }
  
  /* Glassmorphism Tooltip */
  .diagram-tooltip {
    position: absolute;
    pointer-events: none; /* Add this line */

    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 15px 20px;
    background: rgba(30, 30, 30, 0.8);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    min-width: 250px;
    animation: tooltipFadeIn 0.3s ease;
    z-index: 10;
  }
  
  @keyframes tooltipFadeIn {
    from {
      opacity: 0;
      transform: translateX(-50%) translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateX(-50%) translateY(0);
    }
  }
  
  .tooltip-header {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 8px;
  }
  
  .tooltip-icon {
    font-size: 20px;
  }
  
  .tooltip-title {
    font-weight: 600;
    color: #fff;
    font-size: 14px;
  }
  
  .tooltip-description {
    margin: 0;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.7);
    line-height: 1.5;
  }
  
  /* Responsive */
  @media (max-width: 768px) {
    .diagram-svg {
      height: 200px;
    }
    
    .diagram-tooltip {
      position: relative;
      bottom: auto;
      left: auto;
      transform: none;
      margin-top: 15px;
    }
  }