/* ============================================
   INTERACTIVE TIMELINE
   ============================================ */

   .timeline-container {
    padding: 30px;
    background: linear-gradient(135deg, rgba(30, 30, 30, 0.6) 0%, rgba(20, 20, 20, 0.8) 100%);
    border-radius: 16px;
    margin: 25px 0;
    border: 1px solid rgba(255, 255, 255, 0.05);
  }
  
  .timeline-title {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 0 0 40px 0;
    font-size: 18px;
    color: #fff;
  }
  
  .title-icon {
    font-size: 22px;
  }
  
  /* Timeline Track */
  .timeline {
    position: relative;
    height: 120px;
    margin: 0 30px;
  }
  
  .timeline-progress-bg {
    position: absolute;
    top: 25px;
    left: 0;
    right: 0;
    height: 4px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 2px;
  }
  
  .timeline-progress-fill {
    position: absolute;
    top: 25px;
    left: 0;
    height: 4px;
    background: linear-gradient(90deg, #667eea, #764ba2, #22c55e);
    border-radius: 2px;
    transition: width 1s ease;
    box-shadow: 0 0 15px rgba(102, 126, 234, 0.5);
  }
  
  /* Milestone */
  .timeline-milestone {
    position: absolute;
    top: 0;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    z-index: 1;
  }
  
  .timeline-milestone:hover {
    z-index: 10;
  }
  
  /* Milestone Node */
  .milestone-node {
    width: 54px;
    height: 54px;
    border-radius: 50%;
    background: linear-gradient(135deg, #333 0%, #222 100%);
    border: 3px solid #444;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    position: relative;
  }
  
  .milestone-icon {
    font-size: 22px;
    transition: transform 0.3s ease;
  }
  
  .timeline-milestone:hover .milestone-node {
    transform: scale(1.15);
    border-color: #667eea;
    box-shadow: 0 0 25px rgba(102, 126, 234, 0.5);
  }
  
  .timeline-milestone:hover .milestone-icon {
    transform: scale(1.1);
  }
  
  /* Completed State */
  .timeline-milestone.completed .milestone-node {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    border-color: #818cf8;
  }
  
  /* Current State */
  .timeline-milestone.current .milestone-node {
    background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
    border-color: #4ade80;
    animation: currentPulse 2s ease-in-out infinite;
  }
  
  @keyframes currentPulse {
    0%, 100% { box-shadow: 0 0 0 0 rgba(34, 197, 94, 0.4); }
    50% { box-shadow: 0 0 0 10px rgba(34, 197, 94, 0); }
  }
  
  .pulse-ring {
    position: absolute;
    inset: -8px;
    border: 2px solid #4ade80;
    border-radius: 50%;
    animation: pulseRing 2s ease-out infinite;
  }
  
  @keyframes pulseRing {
    0% { transform: scale(1); opacity: 1; }
    100% { transform: scale(1.5); opacity: 0; }
  }
  
  /* Date Label */
  .milestone-date {
    margin-top: 12px;
    font-size: 11px;
    color: rgba(255, 255, 255, 0.5);
    font-weight: 600;
    white-space: nowrap;
  }
  
  /* Tooltip Card */
  .milestone-card {
    position: absolute;
    top: 70px;
    width: 260px;
    padding: 18px;
    background: rgba(30, 30, 30, 0.95);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 14px;
    opacity: 0;
    visibility: hidden;
    transform: translateY(10px);
    transition: all 0.3s ease;
    z-index: 100;
  }
  
  .milestone-card.right {
    left: 50%;
    transform: translateX(-20%) translateY(10px);
  }
  
  .milestone-card.left {
    right: 50%;
    transform: translateX(20%) translateY(10px);
  }
  
  .timeline-milestone:hover .milestone-card,
  .timeline-milestone.active .milestone-card {
    opacity: 1;
    visibility: visible;
    transform: translateY(0) translateX(-50%);
  }
  
  .milestone-card.right:hover,
  .timeline-milestone:hover .milestone-card.right {
    transform: translateX(-20%) translateY(0);
  }
  
  .milestone-card.left:hover,
  .timeline-milestone:hover .milestone-card.left {
    transform: translateX(20%) translateY(0);
  }
  
  .card-header {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 10px;
  }
  
  .card-icon {
    font-size: 20px;
  }
  
  .card-header h4 {
    margin: 0;
    font-size: 15px;
    color: #fff;
  }
  
  .milestone-card p {
    margin: 0 0 12px 0;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.7);
    line-height: 1.5;
  }
  
  .card-status {
    font-size: 11px;
    font-weight: 600;
    padding: 4px 10px;
    border-radius: 12px;
    display: inline-block;
  }
  
  .card-status.completed {
    background: rgba(34, 197, 94, 0.2);
    color: #4ade80;
  }
  
  .card-status.current {
    background: rgba(59, 130, 246, 0.2);
    color: #60a5fa;
  }
  
  /* Legend */
  .timeline-legend {
    display: flex;
    justify-content: center;
    gap: 30px;
    margin-top: 30px;
  }
  
  .legend-item {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.6);
  }
  
  .legend-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
  }
  
  .legend-dot.completed {
    background: linear-gradient(135deg, #667eea, #764ba2);
  }
  
  .legend-dot.current {
    background: #22c55e;
    animation: legendPulse 2s ease-in-out infinite;
  }
  
  @keyframes legendPulse {
    0%, 100% { box-shadow: 0 0 0 0 rgba(34, 197, 94, 0.4); }
    50% { box-shadow: 0 0 0 4px rgba(34, 197, 94, 0); }
  }
  
  /* Responsive */
  @media (max-width: 768px) {
    .timeline-container {
      padding: 20px 15px;
    }
  
    .timeline {
      height: auto;
      margin: 0;
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
  
    .timeline-progress-bg,
    .timeline-progress-fill {
      display: none;
    }
  
    .timeline-milestone {
      position: relative;
      left: 0 !important;
      transform: none;
      flex-direction: row;
      gap: 15px;
    }
  
    .milestone-node {
      width: 45px;
      height: 45px;
      flex-shrink: 0;
    }
  
    .milestone-icon {
      font-size: 18px;
    }
  
    .milestone-date {
      display: none;
    }
  
    .milestone-card {
      position: relative;
      top: 0;
      left: 0 !important;
      right: auto !important;
      transform: none !important;
      opacity: 1;
      visibility: visible;
      width: auto;
      flex: 1;
    }
  }