/* ============================================
   BEFORE/AFTER COMPARISON SLIDER
   ============================================ */

   .before-after-slider {
    position: relative;
    width: 100%;
    height: 320px;
    border-radius: 16px;
    overflow: hidden;
    margin: 25px 0;
    cursor: ew-resize;
    user-select: none;
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.3);
  }
  
  /* Sections */
  .slider-section {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease;
  }
  
  .section-content {
    padding: 30px 40px;
    max-width: 350px;
  }
  
  .section-header {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
  }
  
  .section-icon {
    font-size: 32px;
  }
  
  .section-header h4 {
    margin: 0;
    font-size: 22px;
    font-weight: 700;
  }
  
  .section-points {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .section-points li {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 10px 0;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.85);
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  }
  
  .section-points li:last-child {
    border-bottom: none;
  }
  
  .point-icon {
    font-size: 16px;
    font-weight: bold;
    width: 20px;
    text-align: center;
  }
  
  /* Section Labels */
  .section-label {
    position: absolute;
    top: 20px;
    padding: 6px 16px;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 2px;
    border-radius: 20px;
    border: 2px solid;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(10px);
  }
  
  .before-label {
    left: 20px;
    color: #ef4444;
  }
  
  .after-label {
    right: 20px;
    color: #22c55e;
  }
  
  /* Slider Handle */
  .slider-handle {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 4px;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: ew-resize;
    z-index: 10;
  }
  
  .handle-line {
    flex: 1;
    width: 3px;
    background: linear-gradient(180deg, transparent, #fff, transparent);
    opacity: 0.8;
  }
  
  .handle-circle {
    width: 50px;
    height: 50px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 20px rgba(102, 126, 234, 0.5);
    border: 3px solid #fff;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  
  .handle-circle:hover {
    transform: scale(1.1);
    box-shadow: 0 6px 30px rgba(102, 126, 234, 0.7);
  }
  
  .handle-arrows {
    color: #fff;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 4px;
  }
  
  /* Instructions */
  .slider-instructions {
    position: absolute;
    bottom: 15px;
    left: 50%;
    transform: translateX(-50%);
    padding: 6px 16px;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.6);
    animation: fadeInOut 3s ease infinite;
  }
  
  @keyframes fadeInOut {
    0%, 100% { opacity: 0.6; }
    50% { opacity: 1; }
  }
  
  /* Responsive */
  @media (max-width: 768px) {
    .before-after-slider {
      height: 400px;
    }
  
    .section-content {
      padding: 20px;
    }
  
    .section-header h4 {
      font-size: 18px;
    }
  
    .section-points li {
      font-size: 13px;
      padding: 8px 0;
    }
  
    .handle-circle {
      width: 40px;
      height: 40px;
    }
  }